import styled from "styled-components";
import { motion } from "framer-motion";

export const Backdrop = styled(motion.div)`
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  background: #000000e1;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
`;

export const ProjectContainer = styled(motion.div)`
  position: relative;
  width: 80vw;
  height: 80vh;
  background: white;
  border-radius: 20px;
  display: flex;
  overflow: hidden;

  .test {
    width: 100%;
    height: 100%;
    position: relative;
    display: block;
  }

  .back-btn {
    position: absolute;
    top: -30px;
    right: -30px;
    font-size: 1.5rem;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    background: #fff;
  }

  .header {
    color: red;
    font-size: 20px;
  }
`;

export const LeftSection = styled(motion.div)`
  min-width: min(440px, 25vw);
  max-width: min(440px, 25vw);
  // max-width: 25vw;
  height: 100%;
  // border-right: 1px solid black;
  // border-radius: 10px 0 0 10px;
  position: relative;
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: ;

  .content {
    display: flex;
    flex-direction: column;
  }
  h1 {
    font-size: 20px;
    text-align: center;
    // margin-bottom: 15px;
    margin: 5px auto 10px auto;
  }
  li {
    font-size: 0.9rem;
    margin-bottom: 10px;
  }
  p {
    position: absolute;
    font-size: 12px;
    font-style: italic;
    bottom: 80px;
    opacity: 0.7;
  }

  div {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;

    @media screen and (max-width: 400px) {
      display: block;
      flex-direction: column;
    }
  }
  .button {
    width: min(90px, 16vw);
    font-size: clamp(0.5em, 3vw, 1em);
    // font-size:clamp(40px, 4vw, 5em);
    color: #fff;
    text-decoration: none;
    background: #162936;
    white-space: nowrap;
    outline: none;
    border: none;
    cursor: pointer;
    text-decoration: none;
    transition: 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    padding: 8px 14px;
    border-radius: 10px;
    width: 90px;
    height: min-content;

    @media screen and (max-width: 400px) {
      width: 100%;

      &:nth-child(1) {
        margin-bottom: 10px;
      }
    }
  }
`;

export const RightSection = styled.div`
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  // border-radius: 0px 10px 10px 0px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
`;
export const Title = styled(motion.h1)`
  text-align: center;
  width: 100%;
  font-size: 40px;
`;
